.home-container{
    
    min-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    
}


.intro-text {
    position: relative;
    overflow: hidden;
    
    color: transparent;
    font-size: 2.275em;
    text-transform: uppercase;
    letter-spacing: 3.9px;
    
  }

  
  .intro-text::before {
    content: attr(data-text);
    position: absolute;
    left: 4px;
    text-shadow: rgb(0, 0,0) -1px 0px;
    animation: 15s linear 5s infinite alternate-reverse none running noise-anim-2;
  }
  
  .intro-text::after {
    content: attr(data-text);
    position: absolute;
    left: -2px;
    text-shadow: rgb(255, 105, 180) 3px 0px;
    animation: 2s linear 0s infinite alternate-reverse none running noise-anim;
    
    color: #000;

    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;

  }
  
  mark.mark{
    
    
    padding: 0px;
    background: none 0px 80% / 100% 0.3em no-repeat;
    position: relative;
    color: #000;
  }
  

