.home{
    background-color: #fff;
    background: none;
}

.home-btn{
  margin-top: 1rem;
  border-radius: 5px;
  padding-left: 1rem;
  margin-left: 0.5rem;
  background-color: rgba(255,255,255,0.3);
  border: 1px solid black;
}
.home-btn a{
  color: black;
  text-decoration: none;
  font-weight: 300;
}

@media (max-width:768px){
  .home p{
    width: 95%;
    padding-left:0;
  }
}
